
html::-webkit-scrollbar{display:none
}
.section-title{margin:30px 0px
}
.section-title p.title span.icon .is-danger,.section-title p.subtitle span.icon .is-danger{color:#ff3860
}
.section-title p.title span.icon .is-primary,.section-title p.subtitle span.icon .is-primary{color:#00d1b2
}
#home{margin-top:40px;margin-bottom:80px
}
.window{height:100%
}
.window .bar{width:100%;background-color:#eee;padding:5px;height:30px;border-top-left-radius:5px;border-top-right-radius:5px;border-top:1px solid #DADADA;border-left:1px solid #DADADA;border-right:1px solid #DADADA
}
.window .bar .dot{display:inline-block;margin:2px;width:15px;height:15px;border-radius:50%
}
.window .bar .dot.close{background-color:#F64747
}
.window .bar .dot.expand{background-color:#00d1b2
}
.window .bar .dot.minimize{background-color:#F5AB35
}
.window pre{border-bottom-left-radius:5px;border-bottom-right-radius:5px;height:calc(100% - 30px)
}



