
.window{height:100%;border-radius:5px;box-shadow:-7px 7px 24px -7px #3d3d3d;transition:all 1.5s, height 1.5s, transform 1.5s
}
.window .bar{width:100%;background-color:#eee;padding:5px;height:30px;border-top-left-radius:5px;border-top-right-radius:5px;border-top:1px solid #DADADA;border-left:1px solid #DADADA;border-right:1px solid #DADADA
}
.window .bar .dot{display:inline-block;margin:2px;width:15px;height:15px;border-radius:50%
}
.window .bar .dot.close{background-color:#ff3860
}
.window .bar .dot.expand{background-color:#00d1b2
}
.window .bar .dot.minimize{background-color:#F5AB35
}
.window pre{border-bottom-left-radius:5px;border-bottom-right-radius:5px;height:calc(100% - 30px)
}
.window.minimized{height:30px
}
.hljs{display:block;overflow-x:auto;padding:0.5em;color:#abb2bf;background:#192640
}
.hljs-comment,.hljs-quote{color:#5c6370;font-style:italic
}
.hljs-doctag,.hljs-keyword,.hljs-formula{color:#F5AB35
}
.hljs-section,.hljs-name,.hljs-selector-tag,.hljs-deletion,.hljs-subst{color:#e06c75
}
.hljs-literal{color:#56b6c2
}
.hljs-string,.hljs-regexp,.hljs-addition,.hljs-attribute,.hljs-meta-string{color:#00d1b2
}
.hljs-built_in,.hljs-class .hljs-title{color:#e6c07b
}
.hljs-attr,.hljs-variable,.hljs-template-variable,.hljs-type,.hljs-selector-class,.hljs-selector-attr,.hljs-selector-pseudo,.hljs-number{color:#ff3860
}
.hljs-symbol,.hljs-bullet,.hljs-link,.hljs-meta,.hljs-selector-id,.hljs-title{color:#61aeee
}
.hljs-emphasis{font-style:italic
}
.hljs-strong{font-weight:bold
}
.hljs-link{text-decoration:underline
}



